import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    pageData: null
  },
  mutations: {
    // Set Json Data
    setJsonData (state, json) {
      state.pageData = json
    }
  },
  actions: {
    // Get Json Data
    getJsonData (context, json) {
      context.commit('setJsonData', json)
    },
    // Remove Body Class
    removeBodyClass () {
      const el = document.body
      if (el.className) el.classList.remove('is-menu-visible')
    }
  },
  modules: {
  }
})
